<template>
  <div id="WXPay">
    <navHeader :text="'充值中心'" :back="back"></navHeader>
    <p class="payMoney">￥{{money|moneyFormatType}}</p>
    <p class="payInfo">
      用户
      <span class="phone">{{phone}}</span>
      购买
      <span class="shiGuangBi">{{money}}</span>
      时光币
    </p>
    <van-button type="info" class="buyBtn" @click="pay">确认支付</van-button>
  </div>
</template>
<script>
import navHeader from "../components/navHeader";
import { WXPay } from "../assets/javascript/apis";

export default {
  components: { navHeader },
  data() {
    return {
      money: "",
      phone: "",
      userId: "",
    };
  },
  created() {
    this.money = this.$route.query.money;
    this.phone = this.$route.query.mobile;
    this.userId = this.$route.query.userId;
  },
  methods: {
    pay() {
      let url =
        "https://app.newtiming.com/api/v1/orders/wechat/" +
        this.userId +
        "/" +
        this.money;
      window.location.href = url;
    },
    back() {
      location.replace(location.origin + "/videoApp");
    },
  },
};
</script>
<style lang="scss" scoped>
#WXPay {
  width: 100%;
  height: 100%;
  background: white;
  box-sizing: border-box;
  padding: 0 15px;
  font-family: PingFang SC;
  display: flex;
  flex-direction: column;
  align-items: center;
  .payMoney {
    width: 100%;
    line-height: 40px;
    text-align: center;
    margin-top: 50px;
    font-size: 30px;
    font-weight: 500;
    color: rgba(8, 8, 8, 1);
  }
  .payInfo {
    width: 100%;
    line-height: 20px;
    text-align: center;
    margin-top: 20px;
    font-size: 12px;
    color: rgba(170, 170, 170, 1);
  }
  .buyBtn {
    width: 315px;
    height: 40px;
    margin-top: 40px;
    background: rgba(76, 170, 241, 1);
    border-radius: 10px 10px 10px 10px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(255, 255, 255, 1);
  }
}
</style>